import {
    GET_TAGSWITHSTRUCTURE
} from '../mutation-types'

// 保存我的个人信息
const state = {
    tagsListWithStructure: [],//存放具有标签结构的记录(标签库)
};

// mutations
const mutations = {
    [GET_TAGSWITHSTRUCTURE] (state, tagsListWithStructure) {
        state.tagsListWithStructure = tagsListWithStructure
    }
}

export default {
    state,
    mutations
}


